.categories {
  margin-bottom: 100px;
  width: 416px;
}
.categories__list {
  display: flex;
  flex-direction: column;
  background: #EFEEF6;
  max-width: 416px;
  padding: 50px 0;
}

.categories__list-item { 
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-transform: capitalize;
  color: #5C6A79;
  text-decoration: none;
  padding-left: 160px;
  margin-bottom: 23px;
  transition: all .2s;
}
.categories__list-item:hover {
  transform: scale(1.1);
}
.categories__list-item_active {
  margin-left: 14px;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #1C2A39;
  background: url(/bookstore/static/media/ellipse-bestsellers.d0324fd1.svg) no-repeat left 146px center;
}
.categories__list-item_active:hover {
  transform: scale(1);
}
@media (max-width: 890px) {
  .categories__list-item { 
    font-size: 14px;
    line-height: 17px;
    padding-left: 80px;
  }
  .categories__list-item_active {
    font-size: 18px;
    line-height: 22px;
    background: url(/bookstore/static/media/ellipse-bestsellers.d0324fd1.svg) no-repeat left 68px center;
  }
}
@media (max-width: 816px) {
  .categories__list-item { 
    padding-left: 30px;
  }
  .categories__list-item_active {
    background: url(/bookstore/static/media/ellipse-bestsellers.d0324fd1.svg) no-repeat left 18px center;
  }
}
@media (max-width: 660px) {
  .categories {
    margin: 0 0;
    margin-bottom: 40px;
    width: 100%;
  }
  .categories__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    max-width:  100%;
    padding: 0 10px;
    padding-top: 20px;
  }
}
@media (max-width: 370px) {
  .categories {
    margin-top: 66px;
  }
}
.error {
  position: absolute;left: 50%; 
}
.error__pic {
 margin-bottom: 20px; 
 margin-left: 70px;
}
.error__oops {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
}
.error__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
}
.error__oops, .error__text {
  text-transform: uppercase;
  text-align: center;
}
.spinner {
  position: absolute;
  left: 50%;
}
.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #5C6A79;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
.more {
