  transform: scale(1.05);
  transition: .6s;
}
.book-card__image {
  width: 212px;
  height: 300px;  
  overflow: hidden; 
  box-shadow: 0px 24px 36px rgba(53, 49, 84, 0.28);
}
.book-card__image-pic {
  height: 100%;
  width: 100%;
  background-size: contain;
  transition: .6s;
}
.book-card__image-pic:hover {
  transform: scale(1.05);
  transition: .6s;
}
