  background-color: #dddddd;
}
.cart__info-minus:hover, .cart__info-minus:active {
  background-color:#dddddd;
}
.cart__info-amount {
  margin-right: 10px;
  padding-top: 4px;
}
.cart__info-cost {
  padding-top: 4px;
}

.cart__total-sum {
  text-align: right;
  font-weight: bold;
}
.cart__order {
  margin: 20px 0;
  border-top: 2px dashed #86919e;
}
.cart__order-text {
  margin: 20px 0;
}
.cart__order-form {
  display: flex;
  flex-direction: wrap;
}
.cart__order-email {
  padding-top: 5px;
  border: none;
  border-bottom: 2px solid #c4c2d3;
  color:  #425366;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  width: 100%;
  margin-right: 20px;
  transition: all 1s;
}
